.container{
    height: 100px;
    padding:0px 50px;
    background-color: rgb(240, 101, 187);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 999;
}

.item{
    flex: 1;
    display: flex; 
    align-items: center;
}
.item:nth-child(2){
    flex: 3;
}
.item:nth-child(3){
    justify-content: flex-end;
}

.callButton{
    background-color: rgb(254, 245, 245);
    border-radius: 49%;
    padding: 10px;
    width: 50px;
    height: 50px;
}
.text{
    margin-left: 20px;
    color: rgb(7, 0, 0);
}
.text:first-child{
    font-size: 20px;
    font-weight:500;
}
.text :last-child{
    font-size: 12px;
    font-weight: 500;
}
.list{
    padding: 0;
    list-style: none;
    color: rgb(9, 0, 0);
    display: flex;
    align-items: center;
    cursor:pointer;
}
.listItems{
    padding: 30px;
    font-size: 20px;
    font-weight: 500;
}

.cart{
    position: relative;
}
.counter{
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: rgb(93, 92, 92);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: rgb(255, 255, 255);
}

@media screen and (max-width:480px)  {
    .item:nth-child(2){
        display: none;
    }
    .item:nth-child(1){
        flex: 3; 
    }
}